	module.exports = {"data":{"site":{"siteMetadata":{"title":"rafael.do","author":"Rafael E. Belliard"}},"markdownRemark":{"id":"/Users/rafael/play/rebelliard.github.io/src/pages/blog/2016-01-10-managing-front-end-components-with-bower/index.md absPath of file >>> MarkdownRemark","html":"<p><a href=\"http://bower.io/\">Bower</a> is a package manager for web applications that allows you to install, update and remove web assets (JavaScript, CSS, fonts, etc.) in a consistent manner. Bower also maintains a frequently updated database of all available packages and their corresponding dependencies. <em>Gone</em> are the days of scavenging libraries throughout the internet for a given library on a specific version.</p>\n<h3>Installing Bower</h3>\n<p>Bower depends on <a href=\"https://nodejs.org/\">npm</a> and <a href=\"http://git-scm.com/\">git</a>.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-text\"><code class=\"language-text\">$ npm install -g bower</code></pre>\n      </div>\n<h3>Setting up</h3>\n<p>You can consistently keep track of your setup by creating a <code class=\"language-text\">bower.json</code> file. Bower includes a command for interactively creating it:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-text\"><code class=\"language-text\">$ bower init</code></pre>\n      </div>\n<h3>Searching the repository</h3>\n<p>You can find out which packages are available by either going on the <a href=\"http://bower.io/search/\">online registry</a> or using <a href=\"http://bower.io/docs/api/#search\"><code class=\"language-text\">bower search</code></a>.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-text\"><code class=\"language-text\">$ bower search &lt;package&gt;\n$ bower search jquery</code></pre>\n      </div>\n<h3>Installing packages</h3>\n<p>Install packages using <a href=\"http://bower.io/docs/api/#install\"><code class=\"language-text\">bower install</code></a></p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-text\"><code class=\"language-text\">$ bower install jquery --save\n$ bower install jquery#1.12.0 --save</code></pre>\n      </div>\n<ul>\n<li>The <code class=\"language-text\">--save</code> flag ensures consistency across any device where the bower setup is used by saving any installed dependencies are saved in the <code class=\"language-text\">bower.json</code> file.</li>\n</ul>\n<h3>Using packages</h3>\n<p>All packages are stored in <code class=\"language-text\">/bower_components/</code>. First, you’ll want to check with the installed package’s <code class=\"language-text\">bower.json</code> to see which assets are required, as defined by the <code class=\"language-text\">main</code> element.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-text\"><code class=\"language-text\">{\n  &quot;name&quot;: &quot;jquery&quot;,\n  &quot;version&quot;: &quot;2.1.4&quot;,\n  &quot;main&quot;: &quot;dist/jquery.js&quot;\n}</code></pre>\n      </div>\n<p>Finally, the simplest way to use the downloaded package is to include them directly:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-text\"><code class=\"language-text\">&lt;html&gt;\n  &lt;body&gt;\n    &lt;script src=&quot;/bower_components/jquery/dist/jquery.js&quot;&gt;&lt;/script&gt;\n    &lt;script&gt;\n      $(function () {\n        alert(&#39;Hello Bower!&#39;);\n      });\n    &lt;/script&gt;\n  &lt;/body&gt;\n&lt;/html&gt;</code></pre>\n      </div>","frontmatter":{"title":"Managing front end components with Bower","date":"January 10, 2016","permalink":"/blog/managing-front-end-components-with-bower/"}}},"pathContext":{"slug":"/blog/managing-front-end-components-with-bower/","previous":{"fields":{"slug":"/blog/enabling-scrolling-using-the-microsoft-natural-ergonomic-keyboard-4000s-zoom-slider-on-ubuntu-1210/"},"frontmatter":{"title":"Microsoft Natural Ergonomic Keyboard 4000's zoom slider on Ubuntu 12.10","teaser":null,"description":"Enabling scrolling using the Microsoft Natural Ergonomic Keyboard 4000's zoom slider on Ubuntu 12.10","tags":["setup"],"redirectFrom":null,"disableListing":null}},"next":{"fields":{"slug":"/blog/thoughts-on-giving-my-first-tech-talk/"},"frontmatter":{"title":"Thoughts on giving my first tech talk","teaser":null,"description":"Thoughts on talking about Django Migrations at Python Dominicana 059.","tags":["growth"],"redirectFrom":["/thoughts-on-giving-my-first-tech-talk/"],"disableListing":null}}}}

